.nav-li {

  /* margin-top: 10px; */
  position: relative;
  /* background-color: burlywood; */

}

.nav-li a {
  display: block;
  font-weight: 700;
  font-size: 90%;
  font-size: clamp(50%, 5vw + 10px, .8rem);
  // color: #ABA9B3;
  transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
  -o-transform: all 0.2s ease;
